body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Josefin Sans', sans-serif;
    background-color: lavender;
}

.heading{
    padding: 30px 0;
    text-align: center;
    font-size: 24px;
    text-transform: capitalize;
    background-color: white;
    color: black;
    text-decoration: underline;
    margin-bottom: 30px;
    margin-top: -5px;
}

.cards{
    float: left;
    width: 25%;
    margin: 20px 50px;
    border: 2px solid gray;
    border-radius: 20px;

}

.cards:hover {
    box-shadow: 0 8px 8px 0;
    transform: scale(1.06);
  }

.card{
    background-color: white;
    border-radius: 20px;
}

img{
    width: 100%;
    height: 50vh;
    border-radius: 15px;

}

.card_info{
    text-align: center;
    text-transform: capitalize;
}

.card_catogery{
    color: gray;
}

button{
    padding: 5px 10px;
    margin-bottom: 15px;
    background-color:white;
    color: gray;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid black;
    cursor: pointer;
}

@media (max-width:768px){
    .cards{
       float: none;
       width: 90%;
       margin: 6%;
       
    }
}